<template>
  <div class="infoList">
    <el-form :inline="true" class="demo-form-inline" size="small">
      <el-form-item>
        <el-button type="primary" @click="addStudent">新增</el-button>
      </el-form-item>
    </el-form>
    <el-table :data="compData" border style="width: 100%">
      <el-table-column prop="name" label="姓名" align="center">
      </el-table-column>
      <el-table-column prop="sex" label="性别" align="center">
      </el-table-column>
      <el-table-column prop="age" label="年龄" align="center">
      </el-table-column>
      <el-table-column prop="father" label="父亲" align="center">
      </el-table-column>
      <el-table-column prop="mother" label="母亲" align="center">
      </el-table-column>
      <el-table-column prop="address" label="家庭住址" align="center">
      </el-table-column>
      <el-table-column prop="time" label="入校时间" align="center">
      </el-table-column>
      <el-table-column prop="phone" label="联系方式" align="center">
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button @click="edit(scope.row)" type="info" size="mini" icon="el-icon-edit"></el-button>
          <el-button @click="del(scope.row)" type="danger" size="mini" icon="el-icon-delete"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页实现 -->
    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage"
      :page-sizes="[5, 10, 20, 30, 50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    <!-- 弹窗实现 -->
    <el-dialog :title="state ? '添加学生信息' : '修改学生信息'" :visible.sync="dialogFormVisible" width="500px">
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="姓名" :label-width="formLabelWidth" prop="name">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="性别" :label-width="formLabelWidth" prop="sex">
          <el-radio v-model="form.sex" label="1">男</el-radio>
          <el-radio v-model="form.sex" label="2">女</el-radio>
        </el-form-item>
        <el-form-item label="年龄" :label-width="formLabelWidth" prop="age">
          <el-input v-model="form.age" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="父亲姓名" :label-width="formLabelWidth" prop="father">
          <el-input v-model="form.father" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="母亲姓名" :label-width="formLabelWidth" prop="mother">
          <el-input v-model="form.mother" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="家庭住址" :label-width="formLabelWidth" prop="address">
          <el-input v-model="form.address" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="入校时间" :label-width="formLabelWidth" prop="time">
          <el-date-picker v-model="form.time" format="yyyy 年 MM 月 dd 日" value-format="yyyy-MM-dd" type="date"
            placeholder="选择日期" :picker-options="pickerOptions">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="联系方式" :label-width="formLabelWidth" prop="phone">
          <el-input v-model="form.phone" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="sure('form')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { info, getInfo, updateInfo, infoDel } from '@/api/api.js'
export default {
  data() {
    return {
      tableData: [],
      dialogFormVisible: false,
      form: {
        name: '',
        sex: 1,
        age: '',
        father: '',
        mother: '',
        address: '',
        time: '',
        phone: '',
      },
      formLabelWidth: '80px',
      rules: {
        name: [{ required: true, message: '请输入姓名' }],
        sex: [{ required: true, message: '请输入性别' }],
        age: [{ required: true, message: '请输入年龄' }],
        address: [{ required: true, message: '请输入家庭住址' }],
        time: [{ required: true, message: '请输入入校时间' }],
        phone: [{ required: true, message: '请输入联系方式' }],
      },
      total: 0,
      state: true,
      currentPage: 1, // 当前页数
      pageSize: 5,   // 每页显示的条数

      // 入学日期，日期选择器配置
      pickerOptions: {}  // 还未研究明白怎么配置，先留空
    }
  },
  created() {
    this.getData();
  },
  computed: {
    compData() {
      // 显示页面数据公式：slice((currentPage - 1) * pageSize, currentPage * pageSize)
      // 公式：从第(currentPage - 1) * pageSize条开始，读取到第 currentPage * pageSize -1 条
      return this.tableData.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
    }
  },
  methods: {
    addStudent() {

      this.form = {
        name: '',
        sex: '1',
        age: '',
        father: '',
        mother: '',
        address: '',
        time: '',
        phone: '',
      }
      this.state = true;
      this.dialogFormVisible = true;
      this.$refs['form'].resetFields();
    },
    edit(row) {
      this.state = false;
      this.form = { ...row };
      console.log(this.form);
      this.dialogFormVisible = true;
    },
    del(row) {
      this.$confirm('确认删除该学生信息吗？', '提示', {
        confirmButtonText: '确定',
        callback: () => {
          infoDel(row.id).then(res => {
            console.log(res);
            if (res.data.status === 200) {
              this.getData();
              this.$message({ type: 'success', message: res.data.message });
            }
          })
        }
      })
    },
    getData() {
      getInfo().then(res => {
        console.log(res);
        if (res.data.status === 200) {
          this.tableData = res.data.data;
          this.total = res.data.total;
        }
      })
    },
    sure(form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          console.log(form, this.form);
          // 校验通过，提交表单
          if (this.state) {
            info(this.form).then(res => {
              console.log(res);
              if (res.data.status === 200) {
                this.getData();
                this.dialogFormVisible = false;
                this.$message({ type: 'success', message: res.data.message });
              }
            })
          }
          else {
            updateInfo(this.form).then(res => {
              console.log(res);
              if (res.data.status === 200) {
                this.getData();
                this.dialogFormVisible = false;
                this.$message({ type: 'success', message: res.data.message });
              }
            })
          }
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    // 分页实现
    handleSizeChange(val) {
      this.pageSize = val; // 修改每页显示的条数
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val;  // 修改当前页数
      console.log(`当前页: ${val}`);
    },
  }
}
</script>

<style>
.infoList {

  .demo-form-inline,
  .el-form-item {
    text-align: left;
  }

  .el-pagination {
    margin-top: 20px;
    text-align: left;
  }
}
</style>